<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process216"></a>

<p><br></p>
<p><span class="rvts17">无情的灵魂（</span><span class="rvts50">The Stoic Spirite</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:507,Y:411</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：赛瑞斯</span><span class="rvts20"></span><span class="rvts21">欧兹（</span><span class="rvts20">Syrus Oates</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助无情的灵魂复仇</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1278.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">该任务需要使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，与孤狼营地大门处的一个不会说话的灵魂交谈，得知他的名字叫做赛瑞斯</span><span class="rvts20"></span><span class="rvts21">欧兹（</span><span class="rvts20">Syrus Oates</span><span class="rvts21">）。他生前由于被他旁边的那个守卫皮格斯班（</span><span class="rvts20">Pigsbone</span><span class="rvts21">）活活的烧死而变得无法讲话。</span>
<br><span class="rvts21">因此该任务并不能成功领取，但将来杀掉这个守卫皮格斯班，再次找他谈话，他会感激你。该支线任务会瞬间触发并成功关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



